.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  grid-column-gap: 1em;
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 1em;
  padding-right: 1em;
  left: 0;
  right: 0;
  background-color: #f7f7f5;
  border-radius: 2px;
  margin-top: 1em;
  margin-bottom: 1em;
  margin: 1em calc(auto - 1em);
  padding: 1em; }

.pens {
  grid-column: 1 / -1; }

.pen-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 20px; }

.pen-description {
  -ms-flex: 2 1 200px;
      flex: 2 1 200px;
  margin-right: 20px;
  font-size: 1.3em;
  line-height: 1.2; }

.pen {
  -ms-flex: 3 1 500px;
      flex: 3 1 500px; }

.header {
  margin: 20px 0; }



