.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  grid-column-gap: 1em;
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 1em;
  padding-right: 1em;
  left: 0;
  right: 0;
  position: absolute;
  top: 0;
  z-index: 1; }

.nav {
  padding: 30px 10px; }

.list {
  margin: 0;
  list-style-type: none;
  padding: 0;
  text-align: right; }

.nav-item {
  display: inline-block;
  margin: 0 10px; }

.nav-link {
  text-decoration: none;
  text-transform: uppercase; }



