.section {
  position: relative; }

.container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  grid-column-gap: 1em;
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 1em;
  padding-right: 1em;
  left: 0;
  right: 0;
  justify-items: stretch;
  grid-auto-flow: dense;
  grid-auto-rows: 180px; }

.wrapper {
  -webkit-transition: 0.3s opacity ease-out, 0.3s display ease 0.3s;
  -o-transition: 0.3s opacity ease-out, 0.3s display ease 0.3s;
  transition: 0.3s opacity ease-out, 0.3s display ease 0.3s; }

.hide-container {
  opacity: 0;
  display: none; }

.header {
  grid-column: 1 / -1;
  -ms-flex-item-align: end;
      align-self: flex-end;
  color: #f7f7f5; }

.selected-image {
  -webkit-transform: translateY(-200px);
      -ms-transform: translateY(-200px);
          transform: translateY(-200px);
  z-index: 1; }

.hide-image {
  opacity: 0; }

.image {
  -webkit-transition: 0.2s opacity ease-out, 0.3s -webkit-transform ease-out 0.1s;
  transition: 0.2s opacity ease-out, 0.3s -webkit-transform ease-out 0.1s;
  -o-transition: 0.3s transform ease-out 0.1s, 0.2s opacity ease-out;
  transition: 0.3s transform ease-out 0.1s, 0.2s opacity ease-out;
  transition: 0.3s transform ease-out 0.1s, 0.2s opacity ease-out, 0.3s -webkit-transform ease-out 0.1s; }

.enlarged-image-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  grid-column-gap: 1em;
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 1em;
  padding-right: 1em;
  left: 0;
  right: 0;
  position: absolute;
  top: 0;
  background: #f7f7f5;
  margin: 10px auto;
  z-index: 1;
  border-radius: 3px;
  padding: 1em;
  left: 1em;
  right: 1em; }

.enlarged-figure {
  margin: 0 auto;
  max-width: 1000px;
  overflow: auto;
  height: 90vh;
  width: 100%;
  grid-column: 1 / -1; }

.enlarged-image {
  width: 100%;
  height: 100%;
  cursor: pointer; }
  .enlarged-image.zoomed {
    width: auto;
    height: auto; }

.close-button {
  font-size: 2em;
  grid-column: 1 / -1;
  border: none;
  background: transparent;
  justify-self: flex-end;
  cursor: pointer; }



