/* https://fonts.google.com/specimen/Josefin+Sans?selection.family=Josefin+Sans:100,400,700|Roboto:300,400,500 */
@import url("https://fonts.googleapis.com/css?family=Josefin+Sans:100,400,700|Roboto:300,400,500");
*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

html,
body {
  height: 100%;
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-family: 'Josefin Sans', sans-serif;
  background-color: #1d2658;
  /* More textures at https://www.transparenttextures.com/ */ }

#root {
  display: inline-block;
  width: 100%; }

h1 {
  font-weight: 100;
  font-size: 3rem;
  text-transform: uppercase;
  margin: 10px 0; }

h2 {
  font-weight: 400;
  font-size: 2rem; }

h3 {
  font-weight: 400;
  font-size: 1.17rem;
  text-transform: uppercase; }

figure {
  margin: 0; }

a {
  color: #1d2658; }
  a:visited {
    color: #171e45; }



